let addIntegral = function (delayTime, num, type, success, fail) {
  console.log(`/integral/change?score=${num}&type=${type}`);
  setTimeout(()=>{
	  	 request({
	  		  url:`/integral/change?score=${num}&type=${type}`,
	  		  method:'get'
	  	 }).then(res => {
	  		 console.log(res.data);
	  		 if (res.data.code == 200){
	  			 wx.showToast({
	  				 title:type+'积分+'+num,
	  				 icon:'none'
	  			 })
				 if (type == '登录'){
					 uni.setStorageSync('todayIsGet', getToday())
				 }else if (type == '观看视频'){
					 uni.setStorageSync('videoIsGet', getToday())
				 }
	  		 }
	  	 })
  }, delayTime);
};

