const httpUrl = 'https://www.zafuyanxuetong.cn:8090'
// const httpUrl = 'https://www.zafuyxt.cn:8090'
 // const httpUrl = 'https://10.150.157.156:8090'
export function getToken(code) {
	return new Promise((resolve, reject)=>{
		uni.request({
			url: `${httpUrl}/login/wechatLogin`,
			method: 'POST',
			data: {code:code},
			success: (res) => {
				resolve(res.data.result)
			},
			fail: (err) => {
				reject(err)
			}
		})
	})
}

export const request = (config) =>{
	var token = uni.getStorageSync('token') || ''
	config.url = httpUrl + config.url
	return new Promise((resolve, reject)=>{
		uni.request({
			url: config.url,
			method: config.method,
			data: config.data || {},
			header: { 'authorization': token }
		}).then(res=>{
			resolve(res[1])
		}).catch(err=>{
			reject(err)
		})
	})
}

export const uploadRequest = (config) =>{
	var token = uni.getStorageSync('token') || ''
	return new Promise((resolve, reject)=>{
		wx.uploadFile({
			url: 'https://zafuyxt.cn:8090/upload',
			filePath: config.filePath,
			name: 'filePath',
			header: {
				'authorization': token 
			},
			 success(res) {
			// 	var obj = JSON.parse(res.data)
			// 	resolve(obj)
			console.log(res)
			 },	
			 fail: function(err) {
			 	reject(err)
			 }
		});
	})
}
